<template>
  <div>
    <h1>Yeni Bir ƒ∞sim Ekleyin!</h1>
    <label for="name">ƒ∞sim: </label
    ><input id="name" v-model="newName" type="text" /><br />
    <label for="name">Grup: </label
    ><select name="grup" id="grup" v-model="group"
      ><option value="A">A</option
      ><option value="B">B</option></select
    >
    <button @click="ekle">Ekle</button>
  </div>
</template>

<script>
import EventService from '@/services/EventService'
export default {
  data() {
    return {
      newName: '',
      group: 'A'
    }
  },
  methods: {
    ekle() {
      EventService.createName({ name: this.newName, group: this.group })
        .then(data => {
          console.log(data)
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style scoped>
input {
  font-size: 1em;
  display: inline-block;
  margin: auto;
  margin-bottom: 20px;
}
select {
  font-size: 1em;
}
button {
  margin: auto;
  margin-top: 20px;
  display: block;
  font-size: 1em;
}
</style>
