<template>
  <div>
    <group :array="groupA" groupName="A" />
    <span id="space"></span>
    <group :array="groupB" groupName="B" />
  </div>
</template>

<script>
import Group from '@/components/Group'
import EventService from '@/services/EventService'

export default {
  components: {
    Group
  },
  data() {
    return {
      groupA: [],
      groupB: []
    }
  },
  created() {
    EventService.getA()
      .then(res => {
        this.groupA = res.data
      })
      .catch(error => {
        console.log(error)
      })
    EventService.getB()
      .then(res => {
        this.groupB = res.data
      })
      .catch(error => {
        console.log(error)
      })
  }
}
</script>

<style scoped>
#space {
  width: 50px;
  display: inline-block;
}
</style>
